// import { HttpClientModule } from '@angular/common/http';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Facebook, FacebookLoginResponse } from '@ionic-native/facebook';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  user:any = {};
  userdata: string;

  constructor(public navCtrl: NavController, private fb: Facebook, private http: HttpClient) {

  }

  loginFb(){
    this.fb.login(['public_profile', 'email'])
    .then((res: FacebookLoginResponse) => {
      if(res.status==='connected'){
        this.user.img = 'http://graph.facebook.com/'+res.authResponse.userID+'/picture?type=square';
        this.getData(res.authResponse.accessToken);
      } 
      
      else{
        alert('Login failed');
      }
      console.log('Logged into Facebook!', res)
    })
    .catch(e => console.log('Error logging into Facebook', e));
  }

  getData(access_token: string){
    let url = 'https://graph.facebook.com/me?fields=id,name,first_name,email&access_token=' + access_token;
    this.http.get(url).subscribe(data => {
      this.userdata = JSON.stringify(data);
      console.log(data);
    });
  }

  logout(){
    window.open("http://localhost:8000/index.html", "_self");
    // this.userdata = null;

  }

}



